<template>
	<scroll id="box">
    <p></p>
		<div class="scroll">
			<div class="box" v-for='item,index in data'>
				<p class='title'>{{item.title}}</p>
				<ul class='singerlist' v-if='item.item'>
					<li v-for='item,index in item.item' @click='dat(item)'>
						<img :src="item.pic" alt="">
						<p class='name'>{{item.name}}</p>
					</li>
				</ul>
			</div>
		</div>
		<ul class="btnlist">
			<li v-for='item,index in data' :class='{on: curIndex==index}'>{{item.title}}</li>
		</ul>
	</scroll>
</template>
<script>
	import axios from 'axios';
	import scroll from '@/components/scroll.vue';
	export default{
		data(){
			return {
				data: {},
				curIndex: 0
			}
		},
		mounted(){
			this.getData();
		},
		methods: {
			dat(item){
				axios.get('/apis/singerList').then(data => {
					this.$router.push({
						path: '/details',
						name: 'details',
						component: resolve => require(['@/components/details.vue'],resolve),
						params: {
							title: item.name,
							imgurl: item.pic,
							data: data.data,
							show: 1
						}
					})
				})
			},
			getData(){
				axios.get('/apis/singer').then(data =>{
					console.log(data.data);
					this.data = this.newData(data.data);
					this.data.forEach(item => {
						let obj = {
							images: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${item.albummid}.jpg?max_age=2592000`,
	                        url: `http://dl.stream.qqmusic.qq.com/http://dl.stream.qqmusic.qq.com/C400${item.songmid}.m4a?guid=3076802792&vkey=1F3233DE743300135A199F213BD52CBC5FA2DC2E8E893B7E3B23E6226DE1DC0D088640D671AC54476E07E0111C8BD15CD5F9697DFED861F1&uin=0&fromtag=38
`
						}
						this.$set(item,'newData',obj)
					})
					console.log(this.data)
				})
			},
			newData(data){
				let map = {
					hot: {
						title: '热',
						item: []
					}
				}
				data.data.list.forEach((item,index) => {
					if(index < 10){
						map.hot.item.push({
							name: item.Fsinger_name,
							id: item.Fsinger_id,
							pic: `https://y.gtimg.cn/music/photo_new/T001R300x300M000${item.Fsinger_mid}.jpg`
						})
					}
					let key = item.Findex;
					if(!map[key]){
						map[key] = {
							title: key,
							item: []
						}
					}else{
						map[key].item.push({
							name: item.Fsinger_name,
							id: item.Fsinger_id,
							pic: `https://y.gtimg.cn/music/photo_new/T001R300x300M000${item.Fsinger_mid}.jpg`
						})
					}
				})
				//将对象转成数组
				let ret = [],hot = [];
				for(let key in map){
					if(map[key].title.match(/[a-zA-Z]/)){
						if(map[key].item.length != 0){
							ret.push(map[key]);
						}
					}else if(map[key].title == '热'){
						hot.push(map[key]);
					}
				}
				ret.sort((a,b) => {
					return a.title.charCodeAt()-b.title.charCodeAt();
				})
				return hot.concat(ret);
			}
		},
		components: {
			scroll
		}
	}
</script>
<style scoped>
	.btnlist{
		position: fixed;
		right: 0;
		top: 50%;
		margin-top: -230px;
		background: rgb(27,27,27);
		text-align: center;
		border-radius: 10px;
		padding: 10px 0;
		color: #fff;
	}
	.btnlist li.on{
		color: rgb(241,201,49);
	}
	#box{
		background: rgb(40,40,40);
		height: 100vh;
		overflow: hidden;
	}
	.title{
		background: rgb(53,53,53);
		height: 50px;
		line-height: 50px;
		color: rgb(128,128,128);
		box-sizing: border-box;
		padding-left: 25px;
	}
	.singerlist li{
		height: 70px;
		width: 100vw;
		line-height: 70px;
		vertical-align: middle;
		box-sizing: border-box;
		padding-left: 30px;
	}
	.singerlist img{
		width: 55px;
		border-radius: 50%;
		float: left;
		margin-right: 25px;
	}
	.singerlist .name{
		font-size: 15px;
		color: rgb(135,135,135);

	}
</style>



